import React from "react";
import { Redirect } from "react-router-dom";
import { api, $ } from "../../config";
import R from "../../redux";

export default class JobsList extends React.Component {

    constructor(props) {
        super(props)
        this.state = { 
            page: 1,
            forwardAdd: false,
            forwardShow: false,
            items: []
        }
        R.subscribe(()=>this.setState({items: R.getState().items}))
    }

    componentDidMount() {
        this.setNavbar()
        this.fetchData()
    }

    // Set navbar options
    setNavbar() {
        const { navbar } = this.props;
        if(navbar !== undefined) {
            navbar.setTitle("Trabajos")
            navbar.setAsParent("/jobs-list");
            navbar.setSearchIcon(false);
            navbar.setSearchShow(false, `${api}/jobs/search`)
        }
    }

    // Get data form server
    async fetchData() {
        const { page } = this.state;
        const r = await $.get(`${api}/jobs/fetch/${page}`)
        if(r.error) return alert(r.msg);
        R.dispatch({type: "set-items", data: r.data})
    }

    // Event to get info for lead selected and set in redux
    async jobsDetails(job) {
        const r = await $.get(`${api}/jobs/getone/${job._id}`);
        if(r.error) return alert(r.msg);
        this.props.navbar.hideSE();
        r.data.clientName = job.name;
        await R.dispatch({ type: "set-job", data: r.data })
        await this.setState({forwardShow: true})
    }

    // Function for render the inputs array
    renderItems() {
        const { items } = this.state;
        if(items.length > 0) {
            const items = this.state.items.map(item => (
                <div className="list-view" key={item._id} onClick={() => this.jobsDetails(item)}>
                    <div className="list-item">
                        <div className="list-item-content">
                            <span className="lv-title">{item.name}</span>
                            <span>{item.cell}</span>
                        </div>
                        <div className="list-item-second-content">
                            <span>${item.total.toLocaleString("es-MX", { maximumFractionDigits: 2 })}</span>
                        </div>
                        <div className="list-item-action">{">"}</div>
                    </div>
                </div>
            ))
            return items;
        } else {
            return (
                <div className="no-found" >
                    <span>No hay registros que mostrar</span>
                </div>  
            )
        }
    }

    render() {
        return (
            <div className="container">
                { this.state.forwardAdd && <Redirect to="/select-client" />}
                { this.state.forwardShow && <Redirect to="/job-show" /> }
                
                { this.renderItems() }

                <div className="float-button"
                    onClick={() => this.setState({forwardAdd: true})}
                >
                    {"+"}
                </div>
            </div>
        )
    }

}


