import React from "react";
import R from "../../redux";
import { $, api } from "../../config";
import { Redirect } from "react-router-dom";

export default class ClienJobs extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            navbar: props.navbar,
            forwardShow: false,
            forwardAdd: false,
            items: R.getState().clients.jobs
        }
        R.subscribe(()=> this.setState({items: R.getState().clients.jobs}))
    }

    componentDidMount() {
        this.setNavbar()
    }

    setNavbar() {
        const { navbar } = this.props;
        if(navbar !== undefined) {
            navbar.setTitle("Trabajos Relacionados")
            navbar.setAsChild("/client-jobs")
            navbar.setSubMenuOptions([{title:"Agregar Trabajo", action: ()=> this.addJob()}])
        }
    }

    async addJob() {
        const client = R.getState().clients.values;
        R.dispatch({
            type: "set-job-client-info",
            data: {
                id: client._id,
                name: client.name,
                cellphone: client.cellphone
            }
        })
        this.setState({forwardAdd: true})
    }

    // Event to get info for lead selected and set in redux
    async jobsDetails(job) {
        const r = await $.get(`${api}/jobs/getone/${job._id}`);
        if(r.error) return alert(r.msg);
        r.data.clientName = job.name;
        await R.dispatch({ type: "set-job", data: r.data })
        await this.setState({forwardShow: true})
    }

    // Function for render the inputs array
    renderItems() {
        const { items } = this.state;
        if(items.length > 0) {
            const items = this.state.items.map(item => (
                <div className="list-view" key={item._id} onClick={() => this.jobsDetails(item)}>
                    <div className="list-item">
                        <div className="list-item-content">
                            <span className="lv-title">{item.name}</span>
                            <span>Fetcha de termino: {item.date}</span>
                        </div>
                        <div className="list-item-second-content">
                            <span>${item.total.toLocaleString("es-MX", { maximumFractionDigits: 2 })}</span>
                        </div>
                        <div className="list-item-action">{">"}</div>
                    </div>
                </div>
            ))
            return items;
        } else {
            return (
                <div className="no-found" >
                    <span>No hay registros que mostrar</span>
                </div>  
            )
        }
    }

    render() {
        return(
            <div className="container">
            { this.state.forwardShow && <Redirect to="/job-show" /> }
            { this.state.forwardAdd && <Redirect to="/jobs-add"/> }
                {this.renderItems()}
            </div>
        )
    }

}


