import { createStore } from "redux";

export default createStore((state = {}, action) => {
    switch(action.type) {
        case "user":
            state.user = action.user
        break;

        case "set-leads-values":
            state.leads.values = action.data
        break;

        case "forward-to-client":
            state.leads.forwardToClient = action.forward
        break;

        case "set-clients-values":
            state.clients.values = action.data
        break;

        case "set-client-jobs":
            state.clients.jobs = action.jobs
        break;

        case "set-items":
            state.items = action.data
        break;

        case "set-job-client-info":
            state.jobs.client = action.data
        break;

        case "set-job":
            state.jobs.values = action.data
        break;

        default:
            return state;
    }
    return state;
}, {
    user: {
        token: "",
        logged: false
    },
    items: [],
    leads: {
        values: {},
        forwardToClient: false,
        inputs: [
            {
                name: "name",
                label: "Nombre"
            },
            {
                name: "phone",
                label: "Telefono"
            },
            {
                name: "cellphone",
                label: "Celular"
            },
            {
                name: "address",
                label: "Direccin"
            },
            {
                name: "hood",
                label: "Colonia"
            },
            {
                name: "county",
                label: "Municipio"
            },
            {
                name: "state",
                label: "Estado"
            }
        ]
    },
    clients: {
        values: {},
        jobs: [],
        inputs: [
            {
                name: "name",
                label: "Nombre"
            },
            {
                name: "contact",
                label: "Contacto"
            },
            {
                name: "rfc",
                label: "RFC"
            },
            {
                name: "phone",
                label: "Telefono"
            },
            {
                name: "cellphone",
                label: "Celular"
            },
            {
                name: "address",
                label: "Direccin"
            },
            {
                name: "hood",
                label: "Colonia"
            },
            {
                name: "county",
                label: "Municipio"
            },
            {
                name: "state",
                label: "Estado"
            }
        ]
    },
    jobs: {
        client: {}
    }
})


