import React from "react";
import { Redirect } from "react-router-dom";
import { api, $ } from "../../config";
import R from "../../redux";

export default class SelectClient extends React.Component {

    constructor(props) {
        super(props)
        this.state = { 
            page: 1,
            forwardAdd: false,
            forwardShow: false,
            items: R.getState().items
        }
        this.timeout = null;
        R.subscribe(()=>this.setState({items: R.getState().items}))
    }

    componentDidMount() {
        this.setNavbar()
        this.fetchData()
    }

    // Set navbar options
    setNavbar() {
        const { navbar } = this.props;
        if(navbar !== undefined) {
            navbar.setTitle("Seleccionar Cliente")
            navbar.setAsChild("/select-client");
            navbar.setSearchShow(false, `${api}/clients/search`)
        }
    }

    // Get data form server
    async fetchData() {
        const { page } = this.state;
        const r = await $.get(`${api}/clients/fetch/${page}`)
        if(r.error) return alert(r.msg);
        R.dispatch({type: "set-items", data: r.data})
    }

    clientSelected(i) {
        const { items } = this.state;
        R.dispatch({
            type: "set-job-client-info",
            data: {
                id: items[i]._id,
                name: items[i].name,
                cellphone: items[i].cellphone
            }
        })
        this.setState({forwardAdd: true})
    }

    renderItems() {
        const items = this.state.items.map((item, i) => (
            <div className="list-view" key={item._id} onClick={() => this.clientSelected(i)}>
                <div className="list-item">
                    <div className="list-item-content">
                        <span className="lv-title">{item.name}</span>
                        <span>{item.cellphone}</span>
                    </div>
                    <div className="list-item-action">{">"}</div>
                </div>
            </div>
        ))
        return items;
    }

    render(){
        return(
            <div className="container">
                { this.state.forwardAdd && <Redirect to="/jobs-add"/> }
                <div className="input-group">
                    <input
                        type="text"
                        className="input"
                        placeholder="Buscar cliente"
                        onChange={ ev => {
                            const val = ev.target.value;
                            const se = async () => {
                                const r = await $.get(`${api}/clients/search/${val}`)
                                R.dispatch({type: "set-items", data: r.data})
                            }
                            this.timeout = clearTimeout();
                            this.timeout = setTimeout(se, 800)
                        }}
                    />
                </div>
                { this.renderItems() }
            </div>
        )
    }

}


