import React from "react";
import { Redirect, Link } from "react-router-dom";
import { $ } from "../config";
import R from "../redux";

export default class Navbar extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            user: R.getState().user,
            profile: false,
            show: false,
            parent: true,
            title: "Home",
            sbOptions: [],
            forwardBack: false,
            showSM: false,
            pages: [],
            goBack: "/",
            seVal: "",
            url: "",
            showSearch: false,
            search: true,
            options: [
                {
                    label: "Inicio",
                    link: "/home",
                    icon: "home"
                },
                {
                    label: "Prospectos",
                    link: "/leads-list",
                    icon: "lead"
                },
                {
                    label: "Clientes",
                    link: "/clients-list",
                    icon: "client"
                },
                {
                    label: "Trabajos",
                    link: "/jobs-list",
                    icon: "setting"
                }
            ]
        }

        R.subscribe(()=> this.setState({user: R.getState().user}))
        this.timeout = null
    }

    componentDidMount() {
        let user = localStorage.getItem("user");
        if(user != null) {
            user = JSON.parse(user);
            R.dispatch({
                type: "user",
                user
            })
        }
    }

    // This handle the selected option on side menu
    async selectOption(page, i) {
        const { pages } = this.state;
        await this.setState({ pages: [] })
        pages.push(page);
        this.setActive(i);
        this.setState({show: false, pages})
    }

    setSearchIcon(search) { this.setState({search}) }

    // Set as active the side menu option
    setActive(i) {
        const opt = document.querySelectorAll(".option");
        opt.forEach(o => o.style.background = "#fff")
        opt[i].style.background = "#e2e2e2";
    }

    toggle() {
        alert("Hello from navbar component")
    }

    // Change the bar menu title
    setTitle(title) { this.setState({title}) }

    // Set options from component active, this needs a array with objects
    setSubMenuOptions(sbOptions) { this.setState({sbOptions}) }

    // Set the parameters necessary for search engine, you need pass is dislay and the url
    setSearchShow(home, url) { this.setState({home, url}) }
    
    // Set the bar menu as a main
    async setAsParent(page) {
        this.setState({parent: true, pages: [page]})
    }
    
    // Set the bar menu as child
    async setAsChild(page) {
        const { pages } = this.state;
        if(!pages.includes(page)) pages.push(page);
        this.setState({parent: false, pages})
    }
    
    // Search event for searchEngine
    search(val) {
        const {url} = this.state;
        const se = async () => {
            const r = await $.get(`${url}/${val}`)
            R.dispatch({type: "set-items", data: r.data})
        }
        this.timeout = clearTimeout();
        this.timeout = setTimeout(se, 800)
    }

    // Handle the chango of searchEngine
    async handleSEChange(val) {
        await this.setState({seVal: val})
        await this.search(val)
    }

    // SearchEngine render
    searchEngine() {
        return (
            <div className={(this.state.showSearch ? "search-engine show-search" : "search-engine hide-search")}>
                <i className="icon-back"
                    onClick={() => this.hideSE()}
                ></i>
                <input type="text"
                    ref="se"
                    value={this.state.seVal}
                    onChange={ev => this.handleSEChange(ev.target.value)}
                    placeholder="Realiza tu busqueda"
                />
                <i className="icon-close"
                    onClick={() => this.setState({seVal: ""})}
                ></i>
            </div>
        )
    }

    // Go back page
    async goBack() {
        const { pages } = this.state;
        await pages.pop()
        this.setState({forwardBack: true, pages})
    }

    // Show or hide search engine
    showSE() { this.setState({showSearch: true, seVal: ""}) }
    hideSE() { this.setState({showSearch: false, seVal: ""}) }

    // Log out function
    logOut() {
        const sc = window.confirm("Deseas cerrar sesin?");
        if(sc) {
            const { user } = this.state;
            localStorage.setItem("user", JSON.stringify({}));
            user.logged = false
            this.setState({user, show: false})
        }
        this.setState({show: false})
    }

    // Render side bar component
    renderSideBar() {
        return(
            <aside className={(this.state.show ? "side-bar sb-show" : "side-bar sb-hide")}>
                <div className="side-bar-menu">
                    <div className="profile" >
                        <div className="profile-info" >
                            <Link to="/profile">
                                <img
                                    onClick={()=> this.setState({show: false})}
                                    style={{cursor: "pointer"}}
                                    className="avatar"
                                    src={`/tlacrm/static/img/profiles/${this.state.user.avatar}`}
                                    alt="profile"
                                />
                            </Link>
                            <span>{this.state.user.name}</span>
                        </div>
                    </div>
                    <div className="list-options">
                        <ul>
                            {
                                this.state.options.map( (op, i) => (
                                    <Link to={op.link} key={`${op.label}_${i}`} className="option">
                                        <li
                                            onClick={el => this.selectOption(op.link,i)}
                                        >
                                            <span>{op.label}</span>
                                            <i className={`icon-${op.icon}`}></i>
                                        </li>
                                    </Link>
                                ))
                            }
                            <li
                                onClick={el => this.logOut()}
                            >
                                <span>Cerrar Sesin</span>
                                <i className="icon-logout"></i>
                            </li>
                        </ul>
                    </div>
                </div>
                <div className="side-bar-else"
                    onClick={() => this.setState({show: !this.state.show})}
                ></div>
            </aside>
        )
    }

    // Render parent bar
    renderParentBar() {
        return (
            <div className="navbar">
                <div className="navbar-left-side">
                    <i className="icon-bars"
                        onClick={() => this.setState({show: !this.state.show})}
                    />
                </div>
                <div className="navbar-center-side">
                    {this.state.title}
                </div>
                <div className="navbar-right-side">
                    {this.state.search && (
                            <i className="icon-search"
                                onClick={() => {
                                    this.refs.se.focus()
                                    this.showSE()
                                }}
                            ></i>
                    )}
                </div>
            </div>
        )
    }

    // Render child bar
    renderChildBar() {
        return (
            <div className="navbar">
                <div className="navbar-left-side">
                    <i className="icon-back"
                        onClick={async () => this.goBack()}
                    />
                </div>
                <div className="navbar-center-side">
                    {this.state.title}
                </div>
                <div className="navbar-right-side">
                    {
                        (this.state.sbOptions.length > 0) && (
                            <i className="icon-sub-menu" onClick={()=>this.setState({showSM: true})}></i>
                        )
                    }
                    <div className={(this.state.showSM ? "sm-context" : "")}
                        onClick={()=> this.setState({showSM: false})}
                    ></div>
                    <ul className={(this.state.showSM ? "sub-menu show-sm" : "sub-menu hide-sm")}>
                        {
                            this.state.sbOptions.map((op, i) => (
                                <li onClick={()=> {
                                    op.action()
                                    this.setState({showSM: false})
                                }} key={`${op.title}_${i}`}>
                                    {op.title}
                                </li>
                            ))
                        }
                    </ul>
                </div>
            </div>
        )
    }

    render() {
        const { pages } = this.state;
        return (
            <header>
                {!this.state.user.logged && <Redirect to="/loginpage"/>}
                {this.state.forwardBack && <Redirect to={pages[pages.length - 1]}/>}
                <nav>
                    { this.searchEngine() }
                    {
                        this.state.parent ? this.renderParentBar() : this.renderChildBar()
                    }
                </nav>
                { this.renderSideBar() }
            </header>
        )
    }

}


