import React from "react";
import { Redirect } from "react-router-dom";
import R from "../../redux";
import { api, $ } from "../../config";

export default class LeadsShow extends React.Component {

    constructor(props) {
        super(props)
        this.state = { 
            page: 1,
            forwardAdd: false,
            values: R.getState().leads.values,
            convertToClient: false
        }
        R.subscribe(() => this.setState({values: R.getState().leads.values}))
    }

    componentDidMount() {
        this.setNavbar()
    }

    setNavbar() {
        const { navbar } = this.props;
        if(navbar !== undefined) {
            const options = [
                { title: "Convertir a cliente", action: () => this.convertClient() },
                { title: "Eliminar", action: async () => {
                    const sc = window.confirm("Estas seguro que deseas eliminar el cliente?")
                    if(sc) {
                        const id = this.state.values._id;
                        await $.get(`${api}/leads/remove/${id}`);
                        this.props.navbar.goBack()
                    }
                }}
            ]
            navbar.setTitle("Detalles de Prospecto")
            navbar.setAsChild("/lead-show")
            navbar.setSubMenuOptions(options)
        }
    }

    convertClient() {
        R.dispatch({
            type: "set-clients-values",
            data: this.state.values
        })
        this.setState({convertToClient: true})
    }

    async handleSubmit(e) {
        e.preventDefault();
        const { values } = this.state;
        if(
            values.name === "" ||
            values.name === undefined ||
            values.cellphone === "" ||
            values.cellphone === undefined
        ) return alert("No puedes dejar el nombre y el celular vacios")
        const r = await $.post(`${api}/leads/update`, values);
        if(r.error) return alert(r.msg);
        this.setState({forwardAdd: true})
    }

    handleChange(name, val) {
        const { values } = this.state;
        values[name] = val
        this.setState({values})
    }

    renderInputs() {
        const inputs = R.getState().leads.inputs.map( (el, i)=> (
            <div className="input-group" key={`${el.name}_${i}`}>
                <input
                    type="text"
                    className="input"
                    name={el.name}
                    value={this.state.values[el.name] || ""}
                    id={el.name}
                    placeholder={el.label}
                    onChange={ev => this.handleChange(el.name, ev.target.value)}
                />
            </div>
        ))
        return inputs;
    }

    render() {
        return (
            <div className="container">
                { this.state.convertToClient && <Redirect to="/lead-to-client"/> }
                { this.state.forwardAdd && <Redirect to="/leads-list"/> }
                <form onSubmit={this.handleSubmit.bind(this)}>

                    { this.renderInputs() }
                    
                    <div className="input-group checkbox">
                        <input type="checkbox" className="md-checkbox"
                            id="visited"
                            defaultChecked={this.state.values.visited}
                            onChange={ev => this.handleChange("visited", ev.target.checked)}
                        />
                        <span className="checkmark"></span>
                        <label htmlFor="visited">Visitado</label>
                    </div>
                    
                    <div className="input-group">
                        <textarea value={this.state.values.description || ""}
                            className="textarea"
                            placeholder="Descripcin"
                            onChange={ev => this.handleChange("description", ev.target.value)}
                        />
                    </div>
                    
                    <div className="input-group">
                        <button className="btn btn-primary">Actualizar</button>
                    </div>

                </form>
            </div>
        )
    }

}


