import React from "react";
import { Redirect } from "react-router-dom";
import { api, $ } from "../../config";
import R from "../../redux";

export default class ClientsList extends React.Component {

    constructor(props) {
        super(props)
        this.state = { 
            page: 1,
            forwardAdd: false,
            forwardShow: false,
            items: R.getState().items
        }
        R.subscribe(()=>this.setState({items: R.getState().items}))
    }

    componentDidMount() { this.fetchData() }

    componentWillMount() { this.setNavbar() }

    // Set navbar options
    setNavbar() {
        const { navbar } = this.props;
        if(navbar !== undefined) {
            navbar.setTitle("Clientes")
            navbar.setAsParent("/clients-list");
            navbar.setSearchIcon(true);
            navbar.setSearchShow(false, `${api}/clients/search`)
        }
    }

    // Get data form server
    async fetchData() {
        const { page } = this.state;
        const r = await $.get(`${api}/clients/fetch/${page}`)
        if(r.error) return alert(r.msg);
        R.dispatch({type: "set-items", data: r.data})
    }

    // Event to get info for lead selected and set in redux
    async clientDetails(id) {
        const r = await $.get(`${api}/clients/getone/${id}`);
        if(r.error) return alert(r.msg);
        this.props.navbar.hideSE()
        R.dispatch({ type: "set-clients-values", data: r.data })
        this.setState({forwardShow: true})
    }

    // Function for render the inputs array
    renderItems() {
        const items = this.state.items.map(item => (
            <div className="list-view" key={item._id} onClick={() => this.clientDetails(item._id)}>
                <div className="list-item">
                    <div className="list-item-content">
                        <span className="lv-title">{item.name}</span>
                        <span>{item.cellphone}</span>
                    </div>
                    <div className="list-item-action">{">"}</div>
                </div>
            </div>
        ))
        return items;
    }

    render() {
        return (
            <div className="container">
                { this.state.forwardAdd && <Redirect to="/client-add" />}
                { this.state.forwardShow && <Redirect to="/client-show" /> }
                {
                    this.state.items.length > 0 ? this.renderItems() : (
                        <div className="no-found" >
                            <span>No hay registros que mostrar</span>
                        </div>
                    )
                }

                <div className="float-button"
                    onClick={() => this.setState({forwardAdd: true})}
                >
                    {"+"}
                </div>
            </div>
        )
    }

}


