import React from "react";
import { HashRouter as Router, Route } from "react-router-dom";

// export const host = "http://192.168.0.34:3080";
// export const host = "http://ev-server.ddns.net:3080";
export const api = `/api/tlacrm`;

export const RouteLink = props => (
    <Router>
        <Route
            path={props.path}
            render={props.render}
        />
    </Router>
)

export const $ = {

    async get(url) {
        const options = {
            headers: {
                "Accept": "Application/json",
                "Content-Type": "Application/json",
                "Token": ""
            },
            method: "get"
        }
        let user = localStorage.getItem("user")
        if(user != null) {
            user = JSON.parse(user)
            options.headers.Token =  user.token;

        }
        const request = await fetch(url, options);
        return await request.json();
    },

    async post(url, data) {
        const options = {
            headers: {
                "Accept": "Application/json",
                "Content-Type": "Application/json",
                "Token": ""
            },
            method: "post",
            body: JSON.stringify({data})
        }
        let user = localStorage.getItem("user")
        if(user != null) {
            user = JSON.parse(user)
            options.headers.Token =  user.token;

        }
        const request = await fetch(url, options);
        return await request.json();
    },

    async uploadFile(url, file) {
        const fd = new FormData();
        fd.append("file", file)
        const options = {
            headers: {
                // "Accept": "Application/json",
                // "Content-Type": "Application/json",
                "Token": ""
            },
            method: "put",
            body: fd
        }
        let user = localStorage.getItem("user")
        if(user != null) {
            user = JSON.parse(user)
            options.headers.Token =  user.token;

        }
        const request = await fetch(url, options);
        return await request.json()
    }

}


