import React from 'react';
import { Redirect, HashRouter as Router, Route } from "react-router-dom";
import { RouteLink } from "./config";
import R from "./redux";

import Navbar from "./components/Navbar";
import LoginPage from "./components/login_page";
import Home from "./components/home";
import Profile from "./components/profile";

import LeadsList from "./components/leads/leads_list";
import LeadsAdd from "./components/leads/leads_add";
import LeadsShow from "./components/leads/leads_show";
import LeadToClient from "./components/leads/leads_to_client";

import ClientsList from "./components/clients/clients_list";
import ClientsAdd from "./components/clients/clients_add";
import ClientsShow from "./components/clients/clients_show";
import ClientJobs from "./components/clients/client_jobs";

import JobsList from "./components/jobs/jobs_list";
import SelectClient from "./components/jobs/select_client";
import JobsAdd from "./components/jobs/jobs_add";
import JobShow from "./components/jobs/jobs_show";

export default class App extends React.Component {

  constructor() {
    super()
    this.state = {
      logged: R.getState().user.logged
    }
    R.subscribe(()=> this.setState({logged: R.getState().user.logged}))
  }

  componentDidMount() {
    let user = localStorage.getItem("user");
    if(user != null) {
      user = JSON.parse(user);
      R.dispatch({type: "user", user}) 
    }
  }

  render() {
    const { logged } = this.state;
    return(
      <div>
        <RouteLink path="/" render={() => logged ? <Redirect to="/home"/> : <Redirect to="/loginpage"/>} />
        <RouteLink path="/loginpage" render={() => <LoginPage />}/>
        <Router>
          {
            logged && (
              <div>
                <Navbar ref="navbar" />

                  <Route path="/home" render={() => <Home navbar={this.refs.navbar}/>}/> 
                  <Route path="/profile" render={() => <Profile navbar={this.refs.navbar}/>}/>

                  <Route path="/leads-list" render={() => <LeadsList navbar={this.refs.navbar}/>}/>
                  <Route path="/lead-add" render={() => <LeadsAdd navbar={this.refs.navbar}/>}/>
                  <Route path="/lead-show" render={() => <LeadsShow navbar={this.refs.navbar}/>}/>
                  <Route path="/lead-to-client" render={()=> <LeadToClient navbar={this.refs.navbar}/>}/>

                  <Route path="/clients-list" render={()=> <ClientsList navbar={this.refs.navbar}/>} />
                  <Route path="/client-add" render={() => <ClientsAdd navbar={this.refs.navbar}/>}/>
                  <Route path="/client-show" render={() => <ClientsShow navbar={this.refs.navbar}/>}/>
                  <Route path="/client-jobs" render={()=> <ClientJobs navbar={this.refs.navbar}/>} />

                  <Route path="/jobs-list" render={()=> <JobsList navbar={this.refs.navbar}/>} />
                  <Route path="/select-client" render={()=> <SelectClient navbar={this.refs.navbar} />} />
                  <Route path="/jobs-add" render={()=> <JobsAdd navbar={this.refs.navbar}/>} />
                  <Route path="/job-show" render={() => <JobShow navbar={this.refs.navbar} />} />

              </div>
            )
          }
        </Router>
      </div>
    )
  }

}


