import React from "react";
import { Link } from "react-router-dom";
import { api, $ } from "../config";

export default class Home extends React.Component {

    constructor(props) {
        super(props)
        this.state = {
            leads: 0,
            jobs: 0,
        }
    }

    componentDidMount() { this.fetchData() }

    async fetchData() {
        this.setNavbar()
        const resp = await $.get(`${api}/count-records`)
        if(resp.error) return alert(resp.msg)
        this.setState({leads: resp.data.leads, jobs:resp.data.jobs})
    }

    setNavbar() {
        const { navbar } = this.props;
        if(navbar !== undefined) {
            navbar.setTitle("Inicio")
            navbar.setAsParent();
            navbar.setSearchShow(true)
            navbar.setSearchIcon(false)
        }
    }

    render() {
        return(
            <div className="container card">
                {/* <Redirect to="/jobs-add"/> */}
                <div className="card-item">
                    <Link to="/leads-list">
                        <span>
                            <strong>Prospectos por visitar: <em>{this.state.leads}</em> </strong>
                        </span>
                    </Link>
                </div>
                <div className="card-item">
                    <Link to="/jobs-list">
                        <span>
                            <strong>Trabajos pendientes: <em>{this.state.jobs}</em> </strong>
                        </span>
                    </Link>
                </div>
            </div>
        )
    }

}


