import React from "react";
import { Redirect } from "react-router-dom";
import { api, $ } from "../../config";
import R from "../../redux";
import Modal from "../modal";

export default class JobsShow extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            forward: false,
            addJob: true,
            jobs: [],
            job_val: {
                name: "",
                cant: "",
                cost: ""
            },
            jobTotal: 0,
            payment: 0,
            tax: false,
            navbar: props.navbar,
            values: R.getState().jobs.values
        }

        R.subscribe(() => this.setState({valus: R.getState().jobs.values}))
    }

    componentDidMount() {
        this.setNavbar()
    }

    // Set navbar options
    setNavbar() {
        const { navbar } = this.state;
        navbar.setTitle("Crear Trabajo")
        navbar.setAsChild("/job-show");
        navbar.setSearchShow(true, `${api}/clients/search`)
        navbar.setSubMenuOptions([])
    }

    async handleSubmit() {
        const { values } = this.state;
        const data = {
            id: values._id,
            tax: values.tax,
            jobs: values.jobs,
            payments: values.payments
        }
        const add = await $.post(`${api}/jobs/update`, data)
        if(add.error) return alert(add.msg)
        this.setState({forward: true})
    }

    // Sum all cost from jobs array
    getJobsTotal() {
        const { jobs } = this.state.values;
        if(jobs.length === 0) return { total: 0, formatTotal: "$0" };
        const total = jobs.map(job => job.cant * job.cost).reduce((a, b) => a+b)
        return {
            total,
            formatTotal: "$" + total.toLocaleString("es-MX", { maximumFractionDigits: 2 })
        }
    }

    // Sum all the payments
    getPaymentsTotal() {
        const { payments } = this.state.values;
        if(payments.length === 0) return { total: 0, formatTotal: "$0" };
        const total = payments.reduce((a, b) => parseFloat(a) + parseFloat(b))
        return {
            total,
            formatTotal: "$" + total.toLocaleString("es-MX", { maximumFractionDigits: 2 })
        }
    }

    // Calc function works for calc the subtotal (1), tax(2), total(3)
    calc(n) {
        const subtotal = this.getJobsTotal().total - this.getPaymentsTotal().total;
        let tax;
        
        switch(n) {
            case 1:
                return "$" + subtotal.toLocaleString("es-MX", { maximumFractionDigits: 2 });
            break;
            
            case 2:
                (this.state.values.tax) ? tax = .16 : tax = 0
                return "$" + (this.getJobsTotal().total * tax).toLocaleString("es-MX",  { maximumFractionDigits: 2 })
            break;

            case 3:
                (this.state.values.tax) ? tax = .16 : tax = 0;
                let total = (this.getJobsTotal().total * tax) + this.getJobsTotal().total;
                total = total - this.getPaymentsTotal().total;
                return "$" + total.toLocaleString("es-MX", { maximumFractionDigits: 2 })
            break;

            default:
                console.log(n)
        }

    }

    // Render modal
    renderModal() {
        return (
            <Modal visible={false} ref="modal" mode="fade">
                
                <div className="jobs-container">
                    <span>
                        <i 
                            className="icon-close" 
                            onClick={() => this.refs.modal.toggle()}
                        />
                    </span>
                    
                    <button className="btn btn-primary"
                        style={{marginTop: "1em"}}
                        onClick={() => {
                            this.setState({addJob: true, job_val: {}})
                            this.refs.submodal.toggle()
                        }}
                    >agregar trabajo</button>
                    
                    <ul className="job-content">
                        { this.renderJobs() }
                    </ul>

                </div>

                <Modal visible={false} ref="submodal" mode="bottom">
                    <form className="jobs-container form-group" onSubmit={(ev)=> {
                        ev.preventDefault();
                        const { job_val } = this.state;
                        const { jobs } = this.state.values;
                        if(job_val.name === "" || job_val.cant === "" || job_val.cost === "") return alert("No puedes dejar los campos en blanco")
                        this.state.addJob ? jobs.push(job_val) : jobs[this.state.index] = job_val
                        this.setState({job_val: {}, jobs})
                        this.refs.submodal.toggle()
                    }}>

                        <span>
                            <i 
                                className="icon-close" 
                                onClick={() => this.refs.submodal.toggle()}
                            />
                        </span>

                        <input
                            type="text"
                            placeholder="Nombre del trabajo"
                            className="input"
                            value={this.state.job_val.name || ""}
                            onChange={ev => {
                                const { job_val } = this.state;
                                job_val.name = ev.target.value
                                this.setState({job_val})
                            }}
                        />
                        <input
                            type="text"
                            placeholder="Material"
                            className="input"
                            value={this.state.job_val.mat || ""}
                            onChange={ev => {
                                const { job_val } = this.state;
                                job_val.mat = ev.target.value
                                this.setState({job_val})
                            }}
                        />
                        <input
                            type="number"
                            placeholder="Cantidad"
                            className="input"
                            value={this.state.job_val.cant || ""}
                            onChange={ev => {
                                const { job_val } = this.state;
                                job_val.cant = ev.target.value
                                this.setState({job_val})
                            }}
                        />
                        <input
                            type="number"
                            placeholder="Costo"
                            className="input"
                            value={this.state.job_val.cost || ""}
                            onChange={ev => {
                                const { job_val } = this.state;
                                job_val.cost = ev.target.value
                                this.setState({job_val})
                            }}
                        />
                        <button className="btn btn-primary">
                            {this.state.addJob ? "agregar" : "actualizar"}
                        </button>
                    </form>
                </Modal>

            </Modal>
        )
    }

    // Render modal payments
    renderModalPayments() {
        return (
            <Modal visible={false} ref="modal_payments" mode="fade">
                
                <div className="jobs-container">
                    <span>
                        <i 
                            className="icon-close" 
                            onClick={() => {
                                this.setState({payment: 0})
                                this.refs.modal_payments.toggle()
                            }}
                        />
                    </span>
                    
                    <div className="add-payment">
                        <input
                            type="number"
                            placeholder="Anticipo"
                            className="input"
                            value={this.state.payment || ""}
                            onChange={ev => this.setState({payment: ev.target.value})}
                        />
                        <i className="icon-add" onClick={()=> {
                            const { values } = this.state;
                            if(this.state.payment === 0) return null
                            values.payments.push(this.state.payment)
                            this.setState({values, payment: 0})
                        }}>+</i>
                    </div>
                    
                    <ul className="job-content">
                        { this.state.values.payments.map((pay,i) => (
                            <li className="card-job" key={`${pay}_${i}`}>
                                <div className="first-child">${pay.toLocaleString("es-MX", {maximumFractionDigits: 2})}</div>
                                <i className="icon-trash"
                                    onClick={()=> {
                                        const { values } = this.state;
                                        values.payments.splice(i);
                                        this.setState({values})
                                    }}
                                />
                            </li>
                        )) }
                    </ul>

                </div>

            </Modal>
        )
    }

    // Render Jobs
    renderJobs() {
        if(this.state.values.jobs.length > 0) {
            const jobs = this.state.values.jobs.map((job, i) => (
                <li className="card-job" key={`${job.name}_${i}`}>
                    <div className="first-child"
                        onClick={()=> {
                            this.setState({index: i, job_val: job, addJob: false})
                            this.refs.submodal.toggle()
                        }}
                    >{job.name}</div>
                    <div className="second-child">${
                        (job.cant * job.cost).toLocaleString("es-MX", {maximumFractionDigits: 2})
                    }</div>
                    <i className="icon-trash"
                        onClick={()=> {
                            const { values } = this.state;
                            values.jobs.splice(i);
                            this.setState({values})
                        }}
                    />
                </li>
            ))
            return jobs
        } else {
            return <div>No hay trabajos</div>
        }
    }

    render() {
        return(
            <div className="container job-container">
                { this.state.forward && <Redirect to="jobs-list"/> }
                
                <div className="card" style={{marginTop: "0"}}>
                    <div className="card-item">
                    <strong>Fecha de creacin:</strong>
                    <em>{this.state.values.create_date || ""}</em>
                    </div>
                </div>

                <div className="card" style={{marginTop: "0"}}>
                    <div className="card-item">
                    <strong>Fecha de actualizacion:</strong>
                    <em>{this.state.values.update_date || ""}</em>
                    </div>
                </div>

                <div className="card" style={{marginTop: "0"}}>
                    <div className="card-item">
                    <strong>Cliente:</strong>
                    <em>{this.state.values.clientName || ""}</em>
                    </div>
                </div>
                
                { this.renderModal() }
                { this.renderModalPayments() }

                <div className="card">
                    
                    <div className="card-item"
                        onClick={() => this.refs.modal.toggle()}
                    >
                        <span className="first-child">Trabajos</span>
                        <span className="second-child">{this.getJobsTotal().formatTotal}</span>
                        <i className="icon-add">+</i>
                    </div>

                    <div className="card-item"
                        onClick={() => this.refs.modal_payments.toggle()}
                    >
                        <span className="first-child">Anticipos</span>
                        <span className="second-child">{this.getPaymentsTotal().formatTotal}</span>
                        <i className="icon-add">+</i>
                    </div>

                    <div className="card-item checkbox">
                        <input type="checkbox" className="md-checkbox"
                            id="tax"
                            defaultChecked={this.state.values.tax}
                            onChange={ev => {
                                const { values } = this.state;
                                values.tax = ev.target.checked;
                                this.setState({values})
                            }}
                        />
                        <span className="checkmark"></span>
                        <label htmlFor="tax">Calcular iva</label>
                    </div>

                </div>
                
                <div className="card">
                    <div className="card-item subtotal">
                        <span>Subtotal</span>
                        <span>{this.calc(1)}</span>
                    </div>
                    <div className="card-item tax">
                        <span>IVA</span>
                        <span>{this.calc(2)}</span>
                    </div>
                    <div className="card-item total">
                        <span>Resta</span>
                        <span>{this.calc(3)}</span>
                    </div>
                </div>

                <button className="btn btn-primary mt-1"
                    onClick={()=> this.handleSubmit()}
                >Actualizar</button>

            </div>
        )
    }

}


