import React from "react";
import { Redirect } from "react-router-dom";
import R from "../../redux";
import { api, $ } from "../../config";

export default class ClientsShow extends React.Component {

    constructor(props) {
        super(props)
        this.state = { 
            page: 1,
            forwardAdd: false,
            showJobs: false,
            values: R.getState().clients.values
        }
        R.subscribe(() => this.setState({values: R.getState().clients.values}))
    }

    componentDidMount() {
        this.setNavbar()
    }

    setNavbar() {
        const { navbar } = this.props;
        if(navbar !== undefined) {
            navbar.setTitle("Detalles del Cliente")
            navbar.setAsChild("/client-show")
            navbar.setSubMenuOptions([
                { title: "Mostrar Trabajos", action: () => this.showJobs() }
            ])
        }
    }

    async showJobs() {
        const { _id } = this.state.values;
        const jobs = await $.get(`${api}/clients/get-jobs/${_id}`)
        R.dispatch({ type: "set-client-jobs", jobs: jobs.data })
        this.setState({showJobs: true})
    }

    async handleSubmit(e) {
        e.preventDefault();
        const { values } = this.state;
        if(
            values.name === "" ||
            values.name === undefined ||
            values.cellphone === "" ||
            values.cellphone === undefined
        ) return alert("No puedes dejar el nombre y el celular vacios")
        const r = await $.post(`${api}/clients/update`, values);
        if(r.error) return alert(r.msg);
        this.setState({forwardAdd: true})
    }

    handleChange(name, val) {
        const { values } = this.state;
        values[name] = val
        this.setState({values})
    }

    renderInputs() {
        const inputs = R.getState().clients.inputs.map( (el, i)=> (
            <div className="input-group" key={`${el.name}_${i}`}>
                <input
                    type="text"
                    className="input"
                    name={el.name}
                    value={this.state.values[el.name] || ""}
                    id={el.name}
                    placeholder={el.label}
                    onChange={ev => this.handleChange(el.name, ev.target.value)}
                />
            </div>
        ))
        return inputs;
    }

    render() {
        return (
            <div className="container">
                { this.state.forwardAdd && <Redirect to="/clients-list"/> }
                { this.state.showJobs && <Redirect to="/client-jobs"/> }
                <form onSubmit={this.handleSubmit.bind(this)}>

                    { this.renderInputs() }
                    
                    <div className="input-group">
                        <textarea value={this.state.values.description || ""}
                            className="textarea"
                            placeholder="Descripcin"
                            onChange={ev => this.handleChange("description", ev.target.value)}
                        />
                    </div>
                    
                    <div className="input-group">
                        <button className="btn btn-primary">Actualizar</button>
                    </div>

                </form>
            </div>
        )
    }

}


