import React from "react";
import R from "../redux";
import { $, api } from "../config";
export default class Profile extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            user: R.getState().user,
            selectImg: true,
            image: "",
            url: "",
            loading: false
        }
        R.subscribe(()=> this.setState({user: R.getState().user}))
    }

    componentDidMount() {
        this.setNavbar();
        this.setState({
            url:`/tlacrm/static/img/profiles/${this.state.user.avatar}`
        }) 
    }

    setNavbar() {
        const { navbar } = this.props;
        if(navbar !== undefined) {
            navbar.setTitle("Perfil")
            navbar.setAsParent();
            navbar.setSearchShow(true)
            navbar.setSearchIcon(false)
        }
    }

    handleChange(file) {
        if(file === undefined) {
            this.setState({
                selectImg: true,
                status: "Fallo",
                url: `/tlacrm/static/img/profiles/${this.state.user.avatar}`
            })
            return;
        }
        const reader = new FileReader();
        reader.onload = e => {
            this.setState({url: e.target.result})
        }
        this.setState({selectImg: false, image: file})
        reader.readAsDataURL(file)
    }

    // Function that send the img and store
    async changeImg(input) {
        this.setState({status: "Cambiando la imagen", loading: true})
        const { image } = this.state;
        const resp = await $.uploadFile(`${api}/users/change-avatar`, image)
        if(resp.error) return alert(resp.msg);
        let user = localStorage.getItem("user");
        user = JSON.parse(user);
        user.avatar = resp.avatar;
        localStorage.setItem("user", JSON.stringify(user))
        R.dispatch({type: "user", user})
        this.setState({status: "Imagen guardada exitosamente", loading: false})
    }

    renderAvatar() {
        return (
            <div className="avatar-container" >
                {this.state.loading && <div className="loading"><div className="loader"></div></div>}
                <img
                    src={this.state.url}
                    alt="profile"
                    onClick={()=> this.refs.file.click()}
                />
                {
                    this.state.selectImg ? (
                        <button className="btn btn-success"
                            onClick={()=> this.refs.file.click()}
                        >
                            SELECCIONA UNA IMAGEN 
                        </button>
                    ) : (
                        <button className="btn btn-primary"
                            onClick={()=> this.changeImg()}
                        >
                            CAMBIAR IMAGEN
                        </button>
                    )
                }

                <input
                    ref="file"
                    type="file"
                    style={{display: "none"}}
                    onChange={ev => this.handleChange(ev.target.files[0])}
                />
            </div>
        )
    }

    render() {
        return (
            <div className="container">
                <div className="profile-container">
                    { this.renderAvatar() }
                    <span>{this.state.status}</span>
                </div>
            </div>
        )
    }

}


