import React from "react";
import { Redirect } from "react-router-dom";
import { api, $ } from "../config";
import R from "../redux";

export default class LoginPage extends React.Component {

    constructor() {
        super()
        this.state = {
            username: "",
            password: "",
            logged: false
        }
    }

    componentDidMount() {
        !window.localStorage && alert("Tu navegador no soporta localstorage")
        let user = localStorage.getItem("user");
        if(user != null) {
            user = JSON.parse(user);
            this.setState({logged: user.logged}) 
        }
    }

    async handleSubmit(e) {
        e.preventDefault();
        const {username, password} = this.state;
        if( username === "" || password === "") return alert("No puede dejar campos vacios")
        const response = await $.post(`${api}/auth/login`, {username, password, persistent: true});
        if(response.error) return alert(response.msg)
        const data = {
            id: response.user._id,
            name: response.user.name,
            logged: true,
            token: response.token,
            avatar: response.user.avatar
        }
        await localStorage.setItem("user", JSON.stringify(data))
        await R.dispatch({type: "user", user: data})
        await this.setState({logged: true})
}

    render() {
        return(
            <div className="login-container">

                {this.state.logged && <Redirect to="/home"/>}

                <div className="login-card">
                    <form onSubmit={e => this.handleSubmit(e)}>
                        <h1 className="login-title">Tapiceria los Alamos</h1>
                        <input
                            type="text"
                            placeholder="Usuario"
                            value={this.state.username}
                            onChange={el => this.setState({username: el.target.value})}
                        />
                        <input
                            type="password"
                            placeholder="contrasea"
                            value={this.state.password}
                            onChange={el => this.setState({password: el.target.value})}
                        />
                        <button>Login</button>
                    </form>
                </div>
            </div>
        )
    }

}


